.UserDialog-Wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: hsla(0,0%,0%,0.40);
}

.UserDialog {
    border: 1px solid #aaa;
    border-radius: 4px;
    min-width: 20em;
    background: white;
    box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
}

.UserDialog .signInOrSignUp>nav:first-child {
    padding: 1em;
    margin-left: -2em;
}

.UserDialog .signInOrSignUp>nav:first-child label{
   margin-left: 2em;
   cursor: pointer;    /*鼠标移动到label上变为手*/
}

.UserDialog .forgotPassword>h3{
    padding: 1em 1em .5em 1em;
    text-align: center;
}

.UserDialog .forgotPassword> form{
    padding: 0 1em 1em;
}
.UserDialog form>.row {
    display: flex;
    padding: .1em 0 .3em 0;
}

.UserDialog form>.row>label:first-child {
    width: 4em;
}
.UserDialog form>.row.action {
    padding-left: 4em;
    display: flex;
   align-items: center;
}
.UserDialog button{
    font: inherit;
    padding: .2em;
    margin: 0 auto;
    margin-top: .4em;
    border-radius: 3px;
}